<script>
import Blog from "../Blog.vue";
import imgurl from "../../assets/road.png";
import imgurl1 from "../../assets/submenu.jpeg";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            `,
            css: `
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        ctx.fillStyle = 'green';
        ctx.strokeStyle = 'darkseagreen';
        

        // 天空中的太阳
        ctx.fillStyle = 'skyblue';
        ctx.rect(100, 10, 200, 200);
        ctx.fill();
        ctx.beginPath();
        const sun = ctx.createRadialGradient(200, 110, 5, 200, 110, 30);
        sun.addColorStop(0, '#fff');
        sun.addColorStop(1, 'skyblue');
        ctx.arc(200, 110, 30, 0, (Math.PI/180)*360, false);
        ctx.fillStyle = sun;
        ctx.fill();

        // 绘制文本
        ctx.font = "25px cursive";
        ctx.fillStyle = 'black';
        ctx.fillText("Canvas", 100, 240);
        ctx.strokeText("Canvas", 100, 270);

        // 加载重复图片
        const img = new Image();
        img.src = imgurl;
        img.onload = function() {
            ctx.fillStyle = ctx.createPattern(img, 'repeat');
            ctx.fillRect(320, 10, 200, 200);
        }

        // 渲染图片
        const img1 = new Image();
        img1.src = imgurl1;
        img1.onload = function() {
            ctx.drawImage(img1, 320, 220);
            ctx.drawImage(img1, 530, 10, 100, 100);
            ctx.drawImage(img1, 100, 50, 50, 50, 530, 120, 50, 50);
        }


        // 盒子阴影，填充规则
        // ctx.shadowOffsetX = 3;
        // ctx.shadowOffsetY = 3;
        // ctx.shadowBlur = 5;
        // ctx.shadowColor = "rgba(0, 0, 0, 0.5)";
        // ctx.fillStyle = 'green';
        // ctx.beginPath(); 
        // ctx.arc(350, 20, 20, 0, Math.PI*2, true);
        // ctx.arc(350, 20, 10, 0, Math.PI*2, true);
        // ctx.fill("evenodd");

        

        
    }
};
